.doc{
    display: flex;
}
.doc .content{
    flex: 1;
    padding-left: 50px;
}
.doc ol{
    width: 200px;
    text-decoration: none;
    list-style: none;
    
}
.doc ol li{
    margin-left: 10px;
}
.doc a{
    padding-left: 14px;
}
.doc .active{
    border-left: 4px solid rgb(22, 48, 197);
    color: rgb(15, 65, 228);
    padding-left:10px;
    
}
